import React from "react";
import deleteIcon from "../../../assets/images/delete.png";
import addIcon from "../../../assets/images/add.png";
import SimpleSlider from "./components/Slider";

class Media extends React.Component {
  state = {
    uploading: false,
    imagesToPreview: [],
    imagesToUpload: []
  };

  selectFileHandler = e => {
    this.state.imagesToPreview.push({
      url: window.URL.createObjectURL(e.target.files[0])
    });

    this.state.imagesToUpload.push({
      url: e.target.files[0]
    });

    this.setState({
      imagesToPreview: this.state.imagesToPreview,
      imagesToUpload: this.state.imagesToUpload
    });
    this.props.sendData(this.state.imagesToPreview);
  };

  deleteImage = index => {
    this.state.imagesToPreview.splice(index, 1);
    console.log(1);
    this.setState({
      imagesToPreview: this.state.imagesToPreview
      // imagesToUpload: this.state.imagesToUpload
    });

    this.props.sendData(this.state.imagesToPreview);
  };

  render() {
    const { images } = this.state;
    return (
      <div className="media">
        <h2 className="title">Media</h2>
        <p className="paragraph">
          Choose images for the overview section of your topic.
        </p>
        <div className="gallery-list">
          <div className="gallery upload">
            <div>
              <input
                type="file"
                onChange={this.selectFileHandler}
                id="file-upload"
                style={{ display: "none" }}
                accept="image/png, image/jpeg"
              />

              <label htmlFor="file-upload">
                <img src={addIcon} alt="add" onClick={this.uploadFileHandler} />
              </label>
            </div>
          </div>

          {this.state.imagesToPreview.map((item, index) => {
            return (
              <div
                key={index}
                className="gallery"
                style={{
                  background: `url(${
                    item ? item.url : null
                  }) center/cover no-repeat`
                }}
                // onClick={this.openSlider}
              >
                <img
                  src={deleteIcon}
                  alt="delete"
                  className="delete-img"
                  onClick={() => this.deleteImage(index)}
                />
              </div>
            );
          })}
        </div>

        <button className="G-button" onClick={this.props.moveNextPage}>
          Continue
        </button>
      </div>
    );
  }
}

export default Media;
